.PlanckRendererStyle {
	g.EdgeStyle {
		path {
			// https://www.flagcolorcodes.com/ussr
			stroke: #CC0000;
			stroke-width: 2;
			fill: transparent;
		}
	}
	circle.CircleStyle {
		// https://www.flagcolorcodes.com/ussr
		fill: #CC0000;
		stroke: transparent;
	}
	path.PolygonStyle {
		// https://www.flagcolorcodes.com/ussr
		fill: #CC0000;
		stroke: transparent;
	}
}